body strong {
  font-weight: 900
}
/* 기본 설정 */
html {
  font-size: 16px; /* 기본 1rem = 16px */
}

/* 모바일 */
@media (max-width: 767px) {
  html {
    font-size: 12px; /* FHD: 1rem = 24px (HD의 1.5배) */
  }
}

@media (min-width: 1024px) {
  html {
    font-size: 16px;
  }
}

@media (min-width: 1280px) {
  html {
    font-size: 20px;
  }
}

@media (min-width: 1600px) {
  html {
    font-size: 24px;
  }
}

@media (min-width: 1920px) {
  html {
    font-size: 28px;
  }
}

@media (min-width: 2240px) {
  html {
    font-size: 32px;
  }
}

@media (min-width: 2560px) {
  html {
    font-size: 36px;
  }
}

